import { defineComponent, inject, createVNode } from 'vue';
import FScrollbar from '../../scrollbar/scrollbar.js';
import Draggable from '../../draggable/draggable';
import { provideKey } from '../const';
import Colgroup from './colgroup';
import Header from './header';
import Tr from './tr';
import NoData from './noData';

var bodyTable = defineComponent({
  props: {
    composed: {
      type: Boolean,
      default: false
    },
    columns: {
      type: Array,
      required: true
    }
  },

  setup(props) {
    const {
      layout,
      prefixCls,
      bodyWrapperRef,
      bodyWrapperClass,
      bodyWrapperStyle,
      bodyStyle,
      rootProps,
      showData,
      getRowKey,
      syncPosition,
      scrollbarRef,
      hasFixedColumn,
      onDragstart,
      onDragend,
      beforeDragend
    } = inject(provideKey);

    const renderBodyTrList = () => showData.value.map((row, rowIndex) => createVNode(Tr, {
      "row": row,
      "rowIndex": rowIndex,
      "columns": props.columns,
      "key": getRowKey({
        row
      }) || rowIndex
    }, null));

    const slots = {
      default: _ref => {
        let {
          item,
          index
        } = _ref;
        return createVNode(Tr, {
          "row": item,
          "rowIndex": index,
          "columns": props.columns,
          "expanded": false,
          "key": getRowKey({
            row: item
          }) || index
        }, null);
      }
    };

    const renderBody = () => {
      if (showData.value.length === 0) {
        return createVNode(NoData, null, null);
      }

      if (rootProps.draggable) {
        return createVNode(Draggable, {
          "tag": "tbody",
          "modelValue": showData.value,
          "onUpdate:modelValue": $event => showData.value = $event,
          "beforeDragend": beforeDragend,
          "onDragstart": onDragstart,
          "onDragend": onDragend
        }, slots);
      }

      return createVNode("tbody", null, [renderBodyTrList()]);
    };

    const renderTable = () => {
      return createVNode("table", {
        "class": `${prefixCls}-body`,
        "style": [bodyStyle.value, {
          'table-layout': props.composed ? 'fixed' : rootProps.layout
        }],
        "cellspacing": "0",
        "cellpadding": "0"
      }, [createVNode(Colgroup, {
        "columns": props.columns
      }, null), !props.composed && rootProps.showHeader && createVNode(Header, null, null), renderBody()]);
    };

    const onScroll = e => {
      if (layout.isScrollX.value || layout.isScrollY.value) {
        syncPosition(e);
      }
    };

    return () => {
      if (layout.isScrollX.value || layout.isScrollY.value) {
        return createVNode(FScrollbar, {
          "ref": el => {
            if (el) {
              scrollbarRef.value = el;
              bodyWrapperRef.value = el.$el;
            }
          },
          "class": bodyWrapperClass.value,
          "style": bodyWrapperStyle.value,
          "horizontalRatioStyle": {
            zIndex: 3
          },
          "verticalRatioStyle": {
            zIndex: 3
          },
          "shadowStyle": {
            zIndex: 3
          },
          "shadow": {
            x: hasFixedColumn.value,
            y: true
          },
          "onScroll": onScroll
        }, {
          default: () => [renderTable()]
        });
      }

      return createVNode("div", {
        "ref": el => {
          bodyWrapperRef.value = el;
        },
        "class": bodyWrapperClass.value,
        "style": bodyWrapperStyle.value
      }, [renderTable()]);
    };
  }

});

export { bodyTable as default };
