import 'vue';

const MODE = ['horizontal', 'vertical'];
const COMPONENT_NAME = {
  MENU: 'FMenu',
  SUB_MENU: 'FSubMenu',
  MENU_ITEM: 'FMenuItem',
  MENU_GROUP: 'FMenuGroup'
};
const MENU_KEY = Symbol('FMenu');
const MENU_PROPS = {
  // 当前选中的值
  modelValue: {
    type: [String, Number]
  },
  // 垂直或者水平
  mode: {
    type: String,
    default: MODE[0]
  },
  // 是否收起
  collapsed: {
    type: Boolean,
    default: false
  },
  // 使用反转样式
  inverted: {
    type: Boolean,
    default: false
  },
  // 是否默认展开全部subMenu
  defaultExpandAll: {
    type: Boolean,
    default: false
  },
  // 当前展开的subMenu的key数组
  expandedKeys: {
    type: Array,
    default: () => []
  },
  // 是否只保持一个子菜单展开
  accordion: {
    type: Boolean,
    default: false
  },
  options: {
    type: Array,

    default() {
      return [];
    }

  }
};

export { COMPONENT_NAME, MENU_KEY, MENU_PROPS, MODE };
