import { defineComponent, inject, createVNode } from 'vue';
import { CaretDownOutlined } from '../../icon';
import FCheckbox from '../../checkbox/checkbox.js';
import { provideKey } from '../const';
import Cell from './cell';

var td = defineComponent({
  name: 'FTableBodyTd',
  props: {
    row: {
      type: Object,
      required: true
    },
    rowIndex: Number,
    column: {
      type: Object,
      required: true
    },
    columnIndex: Number,
    onClick: Function
  },

  setup(props) {
    const {
      prefixCls,
      getCellSpan,
      getCellClass,
      getCustomCellClass,
      getCustomCellStyle,
      isSelected,
      isSelectDisabled,
      handleSelect,
      handleExpand,
      getCellValue
    } = inject(provideKey);
    return () => {
      const {
        row,
        column,
        rowIndex,
        columnIndex
      } = props;
      const {
        rowspan,
        colspan
      } = getCellSpan(props);

      if (!rowspan || !colspan) {
        return null;
      }

      return createVNode("td", {
        "rowspan": rowspan,
        "colspan": colspan,
        "style": getCustomCellStyle({
          row,
          column,
          rowIndex,
          columnIndex
        }),
        "class": [`${prefixCls}-td`, ...getCellClass({
          column
        }), ...getCustomCellClass({
          row,
          column,
          rowIndex,
          columnIndex
        })],
        "onClick": props.onClick
      }, [column.props.type === 'default' && createVNode(Cell, {
        "row": row,
        "rowIndex": rowIndex,
        "column": column,
        "columnIndex": columnIndex,
        "cellValue": getCellValue(row, column)
      }, null), column.props.type === 'selection' && createVNode("div", {
        "class": `${prefixCls}-center`
      }, [createVNode(FCheckbox, {
        "modelValue": isSelected({
          row
        }),
        "disabled": isSelectDisabled({
          row
        }),
        "onClick": () => {
          handleSelect({
            row
          });
        }
      }, null)]), column.props.type === 'expand' && createVNode("div", {
        "class": `${prefixCls}-center`
      }, [createVNode(CaretDownOutlined, {
        "class": `${prefixCls}-expand-icon`,
        "onClick": () => {
          handleExpand({
            row
          });
        }
      }, null)])]);
    };
  }

});

export { td as default };
