import { ref, onMounted, watch, nextTick, onBeforeUnmount } from 'vue';
import { ResizeObserver } from '@juggle/resize-observer';

var useResize = (function (triggerRef, callback, disabled) {
  let immediate = arguments.length > 3 && arguments[3] !== undefined ? arguments[3] : true;
  const disabledRef = ref(disabled);

  const handleResize = () => {
    if (disabledRef.value) {
      return;
    }

    if (!immediate) {
      immediate = true;
      return;
    }

    callback && callback();
  };

  const ro = new ResizeObserver(handleResize);
  let observedDom = null;

  const handle = dom => {
    if (observedDom) {
      ro.unobserve(observedDom);
    }

    if (dom) {
      ro.observe(dom);
      observedDom = dom;
    }
  };

  onMounted(() => {
    watch(triggerRef, () => {
      nextTick(() => {
        handle(triggerRef.value);
      });
    }, {
      immediate: true
    });
  });
  onBeforeUnmount(() => {
    ro.disconnect();
  });
});

export { useResize as default };
