import { defineComponent, inject, withDirectives, createVNode, resolveDirective } from 'vue';
import Mousewheel from '../../_util/directives/mousewheel';
import { provideKey } from '../const';
import Colgroup from './colgroup';
import Header from './header';

var headerTable = defineComponent({
  directives: {
    mousewheel: Mousewheel
  },
  props: {
    composed: {
      type: Boolean,
      default: false
    },
    columns: {
      type: Array,
      required: true
    }
  },

  setup(props) {
    const {
      rootProps,
      prefixCls,
      headerWrapperRef,
      headerWrapperClass,
      headerStyle,
      handleHeaderMousewheel
    } = inject(provideKey);
    return () => {
      if (!(props.composed && rootProps.showHeader)) {
        return null;
      }

      return withDirectives(createVNode("div", {
        "ref": el => {
          headerWrapperRef.value = el;
        },
        "class": headerWrapperClass.value
      }, [createVNode("table", {
        "class": `${prefixCls}-header`,
        "style": headerStyle.value,
        "cellspacing": "0",
        "cellpadding": "0"
      }, [createVNode(Colgroup, {
        "columns": props.columns
      }, null), createVNode(Header, null, null)])]), [[resolveDirective("mousewheel"), (e, data) => {
        handleHeaderMousewheel(e, data);
      }]]);
    };
  }

});

export { headerTable as default };
